@font-face {
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/helvetica-neue-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/helvetica-neue-regular.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: cover;
  height: auto;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 390px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (min-width: 1280px) {
  .container {
    min-width: 1280px;
  }
}
@media screen and (min-width: 1600px) {
  .container {
    max-width: 1600px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.page {
  margin: 0;
  height: 1px;
}

.page__body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", "Arial", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
@media screen and (min-width: 1280px) {
  .page__body {
    font-size: 17px;
    line-height: 20px;
  }
}
@media screen and (min-width: 1600px) {
  .page__body {
    font-size: 16px;
    line-height: 22px;
  }
}

.page__main {
  flex: 1;
  height: 100px;
}

.button {
  display: inline-block;
  cursor: pointer;
  font-family: "Helvetica Neue", "Arial", sans-serif;
  border: none;
  font-size: 16px;
  border-radius: 8px;
  font-weight: 500;
  transition: color ease 0.3s, background-color ease 0.3s;
}
.button:hover, .button:focus {
  backdrop-filter: blur(4px);
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}
@media screen and (min-width: 1600px) {
  .button {
    font-size: 18px;
  }
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #202020;
  z-index: 20;
}

.header--white {
  background-color: #fff;
  transition: background-color ease 0.3s;
  border-radius: 0 0 8px 8px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-top: 10px;
  padding-bottom: 10px;
}
@media screen and (min-width: 1280px) {
  .header__container {
    gap: 12px;
  }
}
@media screen and (min-width: 1600px) {
  .header__container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.header__toggle {
  border: none;
  width: 38px;
  height: 38px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4px;
  background-color: rgba(255, 255, 255, 0.1);
}
@media screen and (min-width: 1280px) {
  .header__toggle {
    display: none;
  }
}

.header__toggle span {
  border-radius: 100px;
  width: 20px;
  height: 2px;
  background-color: #fff;
}

.header__toggle--closed {
  background: rgba(32, 32, 32, 0.03);
  position: relative;
}
.header__toggle--closed span {
  background-color: #202020;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.header__toggle--closed span:nth-child(1) {
  transform: translateY(6px) rotate(45deg);
}
.header__toggle--closed span:nth-child(2) {
  opacity: 0;
}
.header__toggle--closed span:nth-child(3) {
  transform: translateY(-6px) rotate(-45deg);
}

.header__menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  transform: translatex(-100%);
  background-color: #f4f4f4;
  width: 100%;
  height: 90vh;
  top: 58px;
  left: 0;
  transition: transform ease 0.3s, opacity ease 0.3s;
  opacity: 0;
  overflow-y: auto;
}
.header__menu--open {
  transform: translateX(0);
  opacity: 1;
}
@media screen and (min-width: 1280px) {
  .header__menu {
    position: static;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 0 13px;
    height: auto;
    width: auto;
    transform: unset;
    opacity: unset;
    background-color: transparent;
    border-radius: 8px;
    overflow: unset;
  }
}
@media screen and (min-width: 1600px) {
  .header__menu {
    gap: 0 41px;
  }
}

.header__connection {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 0 auto;
  max-width: 100%;
  width: 100%;
  row-gap: 16px;
  align-items: center;
  padding: 10px;
  border-radius: 8px 8px 0 0;
  background-color: #fff;
}
@media screen and (min-width: 1280px) {
  .header__connection {
    display: flex;
    width: auto;
    background-color: transparent;
    padding: 0;
    gap: 5px;
    justify-content: flex-end;
  }
}
@media screen and (min-width: 1600px) {
  .header__connection {
    gap: 20px;
  }
}

.header__connection-link {
  grid-column: 1/2;
  grid-row: 1/2;
  font-size: 16px;
  line-height: 14px;
  text-decoration: none;
  color: #202020;
  transition: color ease 0.3s;
}
@media screen and (min-width: 1280px) {
  .header__connection-link {
    color: #fff;
    margin-right: 8px;
  }
}
.header__connection-link:hover, .header__connection-link:focus {
  color: rgba(255, 255, 255, 0.7);
}
@media screen and (min-width: 1600px) {
  .header__connection-link {
    margin-right: 3px;
    font-size: 18px;
  }
}

.header__connection-button {
  grid-column: 1/3;
  grid-row: 2/3;
  background-color: #202020;
  color: #fff;
  padding: 14px;
}
@media screen and (min-width: 1280px) {
  .header__connection-button {
    padding: 10px 13px;
    color: #202020;
    background-color: #fff;
  }
}
@media screen and (min-width: 1600px) {
  .header__connection-button {
    padding: 10px 15px;
  }
}

.header__toggle-language {
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: end;
  cursor: pointer;
}

.logo {
  height: auto;
  color: #fff;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 10px;
  line-height: 9px;
}
.logo:hover[href], .logo:focus {
  scale: 1.1;
  outline: none;
}
.logo:active[href] {
  opacity: 0.6;
}
@media screen and (min-width: 1280px) {
  .logo {
    gap: 2px;
  }
}

.logo__text {
  width: 70px;
}
@media screen and (min-width: 1600px) {
  .logo__text {
    font-size: 12px;
    width: 85px;
  }
}

.logo__image {
  display: block;
  color: #fff;
  width: 69px;
  height: auto;
  object-fit: cover;
  flex-shrink: 0;
}
@media screen and (min-width: 1600px) {
  .logo__image {
    display: none;
  }
}

.logo__desktop-icon {
  display: none;
}
@media screen and (min-width: 1600px) {
  .logo__desktop-icon {
    display: block;
    width: 117px;
  }
}

.logo--black {
  color: #202020;
}

.nav {
  padding-bottom: 98px;
}
@media screen and (min-width: 1280px) {
  .nav {
    padding: 0;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
  }
}

.nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media screen and (min-width: 1280px) {
  .nav__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
    width: auto;
  }
}

.nav__item {
  position: relative;
}
.nav__item:not(:first-child)::before {
  position: absolute;
  content: "";
  width: calc(100% - 20px);
  height: 1px;
  background-color: #e5e5e5;
  display: block;
  top: 0;
  left: 10px;
}
@media screen and (min-width: 1280px) {
  .nav__item:not(:first-child)::before {
    display: none;
  }
}

.nav__link {
  width: 100%;
  display: flex;
  text-decoration: none;
  color: #202020;
  padding: 16px 10px;
  align-items: center;
  justify-content: space-between;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: color ease 0.3s, background-color ease 0.3s;
}
@media screen and (min-width: 1280px) {
  .nav__link {
    padding: 0;
    color: #fff;
    gap: 5px;
    position: relative;
    padding: 11px 6px 11px 8px;
  }
  .nav__link--dropdown {
    padding-right: 10px;
  }
  .nav__link:hover, .nav__link:focus {
    outline: none;
    color: #202020;
    background-color: #fff;
  }
  .nav__link:hover::after, .nav__link:focus::after {
    background-color: #202020;
  }
  .nav__link:active {
    opacity: 0.8;
  }
}
.nav__link--dropdown::after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #e5e5e5;
}
@media screen and (min-width: 1600px) {
  .nav__link {
    font-size: 18px;
    padding: 11px 11px 11px 12px;
  }
  .nav__link--dropdown {
    padding-right: 10px;
  }
}

.nav__link--active {
  font-weight: 500;
  padding-bottom: 4px;
}
@media screen and (min-width: 1280px) {
  .nav__link--active {
    font-weight: 400;
    padding: 11px 10px 11px 8px;
    color: #202020;
    background-color: #fff;
  }
}
@media screen and (min-width: 1600px) {
  .nav__link--active {
    font-size: 18px;
    padding: 11px 10px 11px 12px;
  }
}
.nav__link--active::after {
  background-color: #202020;
}

.nav__link-dropdawn {
  display: none;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media screen and (min-width: 1280px) {
  .nav__link-dropdawn {
    position: absolute;
    width: 260px;
    top: 34px;
    left: 0;
    background-color: #fff;
    border-radius: 8px;
  }
}

.nav__link-dropdawn--open {
  display: block;
}

.nav__link-dropdawn-item {
  width: 100%;
  position: relative;
}
@media screen and (min-width: 1280px) {
  .nav__link-dropdawn-item:not(:first-child)::before {
    content: "";
    display: block;
    height: 1px;
    width: calc(100% - 20px);
    position: absolute;
    top: 0;
    left: 10px;
    background-color: #f4f4f4;
  }
}

.nav__item {
  position: relative;
}

.nav__link--dropdown, .nav__link-dropdawn {
  pointer-events: none;
}

.nav__item:hover .nav__link--dropdown,
.nav__item:hover .nav__link-dropdawn,
.nav__item:focus-within .nav__link--dropdown,
.nav__item:focus-within .nav__link-dropdawn {
  pointer-events: auto;
}

.nav__item:hover .nav__link-dropdawn,
.nav__item:focus-within .nav__link-dropdawn {
  display: block;
}

.nav__dropdawn-link {
  text-decoration: none;
  color: #7b7b7b;
  padding: 14px 20px;
  width: 100%;
  display: block;
  border-radius: 8px;
  font-size: 16px;
  line-height: 16px;
  position: relative;
}
.nav__dropdawn-link:hover, .nav__dropdawn-link:active {
  background-color: #202020;
  color: #fff;
}

.toggle-language {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  background: rgba(32, 32, 32, 0.03);
  border-radius: 8px;
  width: 80px;
  height: 38px;
  overflow: hidden;
  z-index: 2;
  cursor: pointer;
}
@media screen and (min-width: 1280px) {
  .toggle-language {
    background-color: rgba(255, 255, 255, 0.1);
    width: 77px;
  }
}
@media screen and (min-width: 1600px) {
  .toggle-language {
    width: 86px;
    font-size: 16px;
  }
}

.toggle-language__input {
  display: none;
}

.toggle-language__indicator {
  position: absolute;
  background-color: #202020;
  width: 40px;
  height: 38px;
  border-radius: 8px;
  top: 0;
  left: 0;
  transition: transform 0.4s ease;
  z-index: 1;
  transform: translateX(100%);
}
@media screen and (min-width: 1280px) {
  .toggle-language__indicator {
    background-color: #fff;
  }
}

.toggle-language__input:checked + .toggle-language__indicator {
  transform: translateX(0);
}

.toggle-language__option {
  position: relative;
  padding: 8px;
  transition: color 0.4s ease;
  text-align: center;
  z-index: 3;
  font-size: 14px;
  text-transform: uppercase;
}
@media screen and (min-width: 1280px) {
  .toggle-language__option {
    padding: 4px;
  }
}

.toggle-language__input:checked ~ .toggle-language__option.ru {
  color: #fff;
}
@media screen and (min-width: 1280px) {
  .toggle-language__input:checked ~ .toggle-language__option.ru {
    color: #202020;
  }
}

.toggle-language__input:not(:checked) ~ .toggle-language__option.ru {
  color: #7b7b7b;
}
@media screen and (min-width: 1280px) {
  .toggle-language__input:not(:checked) ~ .toggle-language__option.ru {
    color: rgba(255, 255, 255, 0.6);
  }
}

.toggle-language__input:checked ~ .toggle-language__option.en {
  color: #7b7b7b;
}
@media screen and (min-width: 1280px) {
  .toggle-language__input:checked ~ .toggle-language__option.en {
    color: rgba(255, 255, 255, 0.6);
  }
}

.toggle-language__input:not(:checked) ~ .toggle-language__option.en {
  color: #fff;
}
@media screen and (min-width: 1280px) {
  .toggle-language__input:not(:checked) ~ .toggle-language__option.en {
    color: #202020;
  }
}

.footer {
  background-color: #202020;
  padding-top: 27px;
  padding-bottom: 19px;
}
@media screen and (min-width: 1280px) {
  .footer {
    padding-top: 40px;
  }
}
@media screen and (min-width: 1600px) {
  .footer {
    padding-top: 60px;
    padding-bottom: 23px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: auto auto;
  padding-bottom: 16px;
}
@media screen and (min-width: 1280px) {
  .footer__wrapper {
    grid-template-columns: 1fr 2fr 2fr 1fr;
    grid-template-rows: minmax(0, auto) 170px minmax(0, auto) minmax(0, auto) auto auto;
    column-gap: 13px;
    padding-bottom: 8px;
  }
}
@media screen and (min-width: 1600px) {
  .footer__wrapper {
    grid-template-columns: minmax(0, 263px) 488px 503px 243px;
    column-gap: 21px;
    row-gap: 18px;
    padding-bottom: 0;
  }
}

.footer__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  grid-column: 1/3;
}
@media screen and (min-width: 1280px) {
  .footer__top {
    grid-column: 1/2;
    align-items: flex-start;
    gap: 16px;
    margin: 0;
  }
}
@media screen and (min-width: 1600px) {
  .footer__top {
    gap: 30px;
  }
}

.footer__search {
  position: relative;
  width: 100%;
}
@media screen and (min-width: 1600px) {
  .footer__search {
    width: calc(100% - 20px);
  }
}

.footer__search-input {
  position: relative;
  background: rgba(255, 255, 255, 0.1);
  background-image: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: left 10px top 50%;
  padding: 15px 13px 15px 36px;
  border: none;
  border-radius: 8px;
  font-family: "Helvetica Neue", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fff;
  display: block;
  width: 100%;
  cursor: pointer;
}
.footer__search-input::placeholder {
  color: #fff;
}
@media screen and (min-width: 1600px) {
  .footer__search-input {
    background-size: 17px 17px;
    font-size: 18px;
  }
}

.footer__search-button {
  opacity: 0;
  border: none;
  border-radius: 8px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  background-image: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: left 10px top 50%;
  padding: 0;
  top: 50%;
  width: 44px;
  height: 100%;
  right: 0;
  transform: translateY(-50%);
  transition: opacity ease 0.5s;
}

.footer__search-image {
  color: #202020;
  object-fit: cover;
  width: 16px;
  height: 16px;
}

.footer__search:hover .footer__search-button {
  opacity: 1;
}

.footer__connection-button {
  background-color: #fff;
  color: #202020;
  padding: 14px;
  width: 100%;
  margin-bottom: 26px;
  grid-column: 1/3;
}
@media screen and (min-width: 1280px) {
  .footer__connection-button {
    grid-column: 4/5;
    margin: 0;
    height: fit-content;
  }
}

.footer__nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px 10px;
  grid-column: 1/3;
  margin-bottom: 27px;
}
@media screen and (min-width: 1280px) {
  .footer__nav {
    grid-column: 3/4;
    grid-row: 1/5;
    margin: 0;
    row-gap: 0;
  }
}
@media screen and (min-width: 1600px) {
  .footer__nav {
    gap: 13px 33px;
    align-self: start;
  }
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
@media screen and (min-width: 1280px) {
  .footer__contacts {
    gap: 11px;
  }
}
@media screen and (min-width: 1600px) {
  .footer__contacts {
    gap: 17px;
  }
}

.footer__contacts-name {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 20px;
}
@media screen and (min-width: 1280px) {
  .footer__contacts-name {
    font-size: 16px;
  }
}
@media screen and (min-width: 1600px) {
  .footer__contacts-name {
    font-size: 18px;
  }
}

.footer__contacts-item {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
@media screen and (min-width: 1600px) {
  .footer__contacts-item {
    gap: 9px;
  }
}
.footer__contacts-item--links {
  gap: 0;
}
@media screen and (min-width: 1600px) {
  .footer__contacts-item--links {
    gap: 3px;
  }
}

.footer__contacts-link {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  letter-spacing: 1px;
  transition: color ease 0.3s;
}
.footer__contacts-link:hover, .footer__contacts-link:focus {
  color: #7b7b7b;
}
@media screen and (min-width: 1600px) {
  .footer__contacts-link {
    font-size: 18px;
    letter-spacing: -0.1px;
  }
}

.footer__contacts-link + .footer__contacts-link {
  margin-top: -1px;
}
@media screen and (min-width: 1280px) {
  .footer__contacts-link + .footer__contacts-link {
    margin-top: 1px;
  }
}

.footer__contacts-value {
  padding: 0;
  margin: 0;
  color: #fff;
  font-style: normal;
  font-size: 14px;
  line-height: 12px;
}
@media screen and (min-width: 1280px) {
  .footer__contacts-value {
    font-size: 16px;
    line-height: 16px;
  }
}
@media screen and (min-width: 1600px) {
  .footer__contacts-value {
    font-size: 18px;
    line-height: 17px;
  }
}
.footer__contacts-value br {
  display: none;
}
@media screen and (min-width: 1280px) {
  .footer__contacts-value br {
    display: inline;
  }
}

.footer__group {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 3px;
  list-style: none;
}
@media screen and (min-width: 1280px) {
  .footer__group {
    gap: 10px;
  }
  .footer__group--catalog {
    grid-column: 1/2;
  }
  .footer__group--about {
    grid-column: 2/3;
    grid-row: 1/2;
    margin-bottom: 6px;
  }
  .footer__group--other {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media screen and (min-width: 1600px) {
  .footer__group {
    gap: 16px;
  }
  .footer__group--catalog {
    margin-top: -11px;
  }
}

.footer__group-link {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  font-size: 14px;
  max-width: 178px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (min-width: 1280px) {
  .footer__group-link {
    font-size: 16px;
    line-height: 20px;
    overflow: unset;
  }
}
@media screen and (min-width: 1600px) {
